.card {
  background: #fff;
  border-radius: 2px;
  height: 245px;
  margin: 1rem;
  position: relative;
  float: left;
  width: 245px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:white;
}
.card > .img-container > img {
  width: 100%
}

.card:hover {
  -webkit-box-shadow: 0 0 11px rgba(33,33,33,.2);
          box-shadow: 0 0 11px rgba(33,33,33,.2); 
}








